import uniLoadMore from '@/colorui/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	onShow() {
		this.page = 1;
		this.drivers = [];
		this.loadData();
	},
	data() {
		return {
			page: 1,
			pageSize: 10,
			status: 'more',
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			drivers: []
		}
	},
	onReachBottom() {
		if(this.status !== "noMore"){
			this.status = 'more';
			this.loadData();
		}
	},
	methods: {
		loadData(){
			this.status = 'loading';
			global.$http.post('/car/driver/driverList', {
				params: {
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					if(res.data.totlePage <= this.page){
						this.status = 'noMore';
					}else{
						this.status = "more";
					}
					if(this.page === 1){
						this.drivers = res.data.list;
					}else{
						res.data.list.forEach(c=>{
							this.drivers.push(c);
						});
					}
					this.page++;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		driverDetail: function(driver){
			uni.navigateTo({
				url: "../work/driverDetail?id=" + driver.id,
			});
		}
	}
}
